


.jumbotron {
    background-image: -webkit-gradient( linear,left top, left bottom,from(rgb(5, 0, 70)),to(rgb(205, 202, 243)));
    background-image: -webkit-linear-gradient( rgb(5, 0, 70),rgb(205, 202, 243));
    background-image: -o-linear-gradient( rgb(5, 0, 70),rgb(205, 202, 243));
    background-image: linear-gradient( rgb(5, 0, 70),rgb(205, 202, 243));
    height: auto;
    padding: 50px;
    margin-top: 150px;
    border-radius: 25px;
    
}


