.subtitle {
    background-image: -webkit-gradient( linear,left top, left bottom,from(rgb(5, 0, 70)),to(rgb(30, 19, 190)));
    background-image: -webkit-linear-gradient( rgb(5, 0, 70),rgb(30, 19, 190));
    background-image: -o-linear-gradient( rgb(5, 0, 70),rgb(30, 19, 190));
    background-image: linear-gradient( rgb(5, 0, 70),rgb(30, 19, 190));
    padding: 15px;
    border-radius: 5px;
    text-align: center;
    color: white;
    opacity: 0.9;
    font-family: 'Frijole', cursive;
    font-size: 25px;
}


